import React from 'react';
import withStyles from 'isomorphic-style-loader/lib/withStyles';
import s from './Profile.css';
import image from './pashaProfile.jpg';

class Profile extends React.Component {
  render() {
    return (
      <div className={s.profile}>
        <div className={s.title}>
          <h2>Profile</h2>
          <span>I'm a creative Front-End developer</span>
        </div>
        <div className={s.mainIfo}>
          <div className={s.aboutMeInf}>
            <h3>About me</h3>
            <p>
              I'm a responsible and self-confident person I love structure and
              order and I also stand for quality. I love spending time on fixing
              little details and optimizing web apps. Also I like working in a
              team
            </p>
          </div>
          <img className={s.imageProfile} src={image} alt="" />
          <div className={s.details}>
            <h3>Details</h3>
            <ul className={s.detailsList}>
              <li>
                <strong>Name:</strong>
              </li>
              <li>Paul Gnydyn</li>
              <li>
                <strong>Age:</strong>
              </li>
              <li>19</li>
              <li>
                <strong>Location</strong>
              </li>
              <li>Kiev,Ukraine</li>
            </ul>
          </div>
        </div>
      </div>
    );
  }
}

export default withStyles(s)(Profile);



