import React from 'react';
import Profile from '../../components/Profile/Profile';
import Layout from '../../components/Layout';

const title = 'Profile';

function action() {
  return {
    chunks: ['profile'],
    title,
    component: (
      <Layout Header="Profile">
        <Profile />
      </Layout>
    ),
  };
}

export default action;



